/* Общие стили */
.App {
  text-align: center;
  font-family: Arial, sans-serif;
  color: #ecf0f1;
  background-color: #2c3e50;
  height: 100vh;
  overflow: hidden;
}

/* Адаптивные стили для мобильных устройств */
@media (max-width: 768px) {
  .click-target {
    width: 30vw; /* Измените размер целевого изображения на 30% от ширины экрана */
  }

  .score {
    font-size: 6vw; /* Увеличьте размер шрифта для счетчика нажатий */
    padding: 10px;
  }

  .loading-logo {
    width: 40vw; /* Увеличьте размер логотипа на загрузочном экране */
  }

  .progress-bar {
    width: 80%;
    height: 6vw; /* Увеличьте высоту полосы загрузки */
  }

  .start-menu button {
    font-size: 5vw; /* Увеличьте шрифт кнопок на стартовом экране */
    padding: 3vw 6vw; /* Сделайте кнопки больше */
    margin: 3vw;
  }

  .bonus-item {
    width: 15vw; /* Установите размер бонусного предмета */
  }
}
